import { searchStarCnt } from "@/api/teacher";

const state = {
  starData: []
};

const mutations = {
  SAVE_STAR_CNT(state, data) {
    state.starData = data;
  }
};

const actions = {
  //课程考勤详情
  searchStarCnt({ commit }, id) {
    return new Promise(resolve => {
      searchStarCnt(id).then(res => {
        commit("SAVE_STAR_CNT", res.data);
        resolve(res);
      });
    });
  }
};

export default {
  namespaced: true,
  mutations,
  actions,
  state
};



