import request from "@/utils/request";
//订单管理
export function manageOrders(data) {
  return request({
    url: `/agency/front/Order/manageOrders?${data}`,
    method: "get"
  });
}
//查看订单操作记录
export function queryOrderOperation(data) {
  return request({
    url: `/agency/front/Order/queryOrderOperation`,
    method: "get",
    params:data
  });
}


