import { queryUserMenu } from "@/api/user";

const actions = {
  searchLeftMenu({ commit }) {
    return new Promise(resolve => {
      queryUserMenu()
        .then(res => {
          commit("SAVE_MENU", res.data);
          resolve(res)
        })
        .catch(err => {
          console.err(err);
        });
    });
  }
};

export default actions;



